

section#contact{
	// height: 100vh;
	background-color: $blue;
	@include desktop{
		background-color: $blue;
		flex-direction: row;
		height: 70vh;
		// margin-bottom: 100px;

		div.desktop{
			background-color: white;
			position: absolute;
			height: 35vh;
			width: 100%;
			bottom: 0;
			z-index: 0;
		}
		*{
			z-index: 1;
		}
	}
	> div:first-of-type{
		display: grid;
		grid-template-areas:
		"h1 logo"
		"p logo";
		align-items: center;
		// justify-content: center;
		padding-bottom: 2rem; 
		@include desktop {
			display: flex;
			flex-direction: column;
			// max-width: 600px;
			width: 45%;
			justify-content: space-between;
			padding: 2rem auto 0 auto;
			// padding-bottom: 0;
			margin: 0 auto;
			height: 100%;

		}

		h1{
			grid-area: h1;
			font-weight: bold;
			@include desktop{
				max-width: none;
				width: auto;
			};
		}
		p{
			grid-area: p;
			font-size: 1.6rem;
			@include desktop{
				margin: 0;
				max-width: none;
				width: auto;
				font-size: 2.4rem;

			}

		}
		> img{
			grid-area: logo;
			align-self: center;
			justify-self: center;
			width: 80%;
			margin-bottom: 0%;
			@include desktop {
				// height: 60%;
				// width: 80%;
				// margin-bottom: -30%;
			}
		}
	}
	> div:last-of-type{
		@include desktop{
			margin: 0rem auto 0 auto;
			background: none;
			// position: relative;
			height: 100%;
		}
		padding: 2rem 10vw;
		background: white;

		form{
			@include desktop{
				height: 100%;
				position: relative;
				// max-height: 500px;
			}
			// padding: 2rem;
			> div{
				@include desktop{
					height: 80%;
					// max-height: 400px;
					row-gap: 0px;
				}
				background: $blue;
				padding: 1rem 1rem 2rem 1rem;
				border-radius: 5px;
				display: grid;
				grid-gap: 1rem;
				grid-template-columns: 1fr 1fr;
				grid-template-rows: repeat(4, auto);
				div{
					display: flex;
					flex-direction: column;
					height: 100%;
					// padding: 1rem;
					label,
					input
					{
						display: block;
						width: 100%;
						font-size: 2rem;
						border-radius: 3px;
						border: none;
						@include desktop{
							font-size: 2rem;
						}
					}
					input{
						padding: 1rem;
						// border: 2px	white solid;
						@include desktop{
							height: 4rem;

						}
					}
					input:focus{
						// border: 2px	$light-blue solid;
						 box-shadow: 0 0 4px 0pt black;
					}
					select{
						// display: block;
						// width: 100%;
						background: white;
						font-size: 2rem;
						border-radius: 3px;
						border: none;
						-webkit-appearance: none;
						 -moz-appearance: none;
						 background-image: url("data:image/svg+xml;utf8,<svg fill='gray' height='30' viewBox='0 0 30 30' width='30' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h30v30H0z' fill='none'/></svg>");
						 background-repeat: no-repeat;
						 background-position-x: 0%;
						 background-position-y: 1rem;
						 border: none;
						 border-radius: 3px;
						 height: 4rem;
						 // padding: 0.7rem;
						 padding-right: 1rem;
					}
				}
				div:last-child,
				div:nth-last-child(2)
				{
					background: none;
					width: 100%;
					grid-column: 1 / 3;
				}
			}
			input::-webkit-outer-spin-button,
			input::-webkit-inner-spin-button {
			  -webkit-appearance: none;
			  margin: 0;
			}

			/* Firefox */
			input[type=number] {
			  -moz-appearance: textfield;
			}

			.hidden{
				opacity: 0;
				// transition: opacity 700ms;
				pointer-events: none;
			}
			> div:last-of-type{
				// display: none;
				position: absolute;
				// height: 50%;
				width: 70%;
				top: 50%;
				left: 50%;
				font-size: 5rem;
				font-weight: bold;
				opacity: 0;
				pointer-events: none;
				transform: translate(-50%, -30%);
				text-align: center;
				transition: opacity 400ms 200ms;

			}

			> div:last-of-type.show{
				display: block;
				opacity: 1;
				pointer-events: all;
				transform: translate(-50%, -30%);
			}
		}
		p{
			color: $blue
		}

	}

			[name="submit"],
			a.whatsapp{
				margin: 1rem 0;
				width: 100%;
				border: none;
				color: white;
				background: $blue;
				border-radius: 3px;
				font-weight: 500;
				font-family: "Abraham";

				display: flex;
				justify-content: center;
				align-items: center;
				padding: 1rem 0;
				cursor: pointer;
				@include desktop{
					padding: 0rem 0;
					height: 50px;
					margin-top: 1rem;
					margin-bottom: 0;
					font-size: 3rem;
				}

			}

			[name="submit"]{
				font-size: 3rem;
				@include desktop{
					position: absolute;
					bottom: 0;
					transition: background 100ms, box-shadow 200ms, color 100ms, transform 200ms;

				}
			}
			[name="submit"]:focus,
			[name="submit"]:hover{
				background: white;
				box-shadow: 0px 4px 2px 1px rgba(0,0,0,0.3);
				color: $blue;
				transform: translateY(-3px);
			}
			a.whatsapp{
				background: $green;
				border: none;
				margin-top: 30px;
				transition: background 100ms, box-shadow 200ms, transform 200ms, color 200ms;
				display: flex;
				align-items: center;
				font-size: 2rem;

				svg{
					height: 2.5rem;
					margin: 0 -3px;
				}
				path{
					transition:	fill 100ms;

				}
				@include desktop{
					// margin-top: 90px;


					img{
						height: 3rem;
						// width: 10%;

					}
				}
			}
			a.whatsapp:hover,
			a.whatsapp:focus{
				background: white;
				// border: 2px $green solid;
				box-shadow: 0px 4px 2px 1px rgba(0,0,0,0.3);
				transform: translateY(-3px);
				color: $green;
				path{
					fill: $green;
				}
			}
			a.desktop-btn{
				display: none;
				@include desktop{
					display: flex;
				}
			}


}
